<template>
  <v-container>
    <v-row
      v-for="(group, i) in groups"
      :key="i"
      class="pointer"
      justify="start"
      no-gutters
      @click="setId(group.id)"
    >
      <v-col cols="auto">
        <v-card outlined class="pa-2 text" tile style="color:red;">{{
          group.grouping
        }}</v-card>
      </v-col>
      <v-col cols="auto" v-for="(word, index) in group.wordList" :key="index">
        <v-card outlined class="pa-2 text" tile>{{ word }}</v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapState } from "vuex"

export default {
  name: "About",
  computed: {
    ...mapState(["groupId", "groups"]),
  },
  data: () => ({}),
  mounted() {},
  methods: {
    setId(id) {
      this.$store.dispatch("setValue", { key: "groupId", value: id })
      this.$router.push("/")
    },
  },
}
</script>
<style>
.text {
  font-size: 2em;
  height: 2em;
}
</style>
